.footer{
    width: 100%;
    height: 619px;
    padding-top: 60px;
    padding-bottom: 60px;

    background-image: url(../images/footer/footer-bg@1x_sm.png);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;

    box-shadow: 0px -4px 24px rgba(105, 105, 105, 0.1);

    @media (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
            background-image: url(../images/footer/footer-bg@2x_sm.png);
        }

    @media screen and (min-width: 768px) {
        height: 506px;
        padding-top: 100px;
        padding-bottom: 100px;

        background-image: url(../images/footer/footer-bg@1x_md.png);
        
        @media (min-device-pixel-ratio: 2),
            (min-resolution: 192dpi),
            (min-resolution: 2dppx) {
                background-image: url(../images/footer/footer-bg@2x_md.png);
        }
    }

    @media screen and (min-width: 1280px) { 
        
        background-image: url(../images/footer/footer-bg@1x_lg.png);
    
        @media (min-device-pixel-ratio: 2),
            (min-resolution: 192dpi),
            (min-resolution: 2dppx) {
                background-image: url(../images/footer/footer-bg@2x_lg.png);
        }
    }
}

.footer__main{
 
    @media screen and (min-width: 1280px){
        display: flex;
        flex-wrap: wrap;
        align-items: flex-start;
    }
}
.footer__block{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    align-items: center;

    @media screen and (max-width: 767px) {
        align-items: flex-start;
    }

    @media screen and (min-width: 768px) {
        gap: 114px;
    }

    @media screen and (min-width: 1280px) {
        gap: 140px;
    }
}

.footer__logo{

    @media screen and (max-width: 767px) {
        flex-basis: 50%;
    }
    @media screen and (min-width: 1280px) {
        width: 100%;
        margin-left: auto;
    }

}
.footer__menu{
    @media screen and (max-width: 767px) {
        flex-basis: 50%;
    }
}


.footer__bottom{
    margin-top: 60px;

    @media screen and (min-width: 768px){
        margin-top: 100px;
    }

    @media screen and (min-width: 1280px){
        margin-top: 80px;
    }
}

.footer__subscribe {

    //width: 100%;
    //margin-top: 0;
    @media screen and (max-width: 1279px) {
        width: 100%;
        margin-top: 40px;
    }

    @media screen and (min-width: 1280px) {
        margin-left: auto;
    }
}

.footer-subscribe__title{
    font-weight: 500;
    line-height: calc(21 / 14);

    color: var(--main-color-text);
}

.footer-subscribe__text{

    font-size: 10px;
    line-height: calc(12 / 10);
    color: var(--main-color-text);
    margin-top: 20px;

    & a{
        color: var(--color-link);
    }

    @media screen and (min-width: 768px) {
        font-size: 12px;
        line-height: 14px;
        margin-top: 12px;
    }
}


.subscribe__form-modifire {
    @media screen and (min-width: 768px) {
        display: flex;
        gap: 12px;
        margin-top: 12px;
    }
}